<!--  -->
<template>
  <div class="side-bar">
    <el-input
      class="filter-input"
      size="mini"
      suffix-icon="el-icon-search"
      placeholder="输入关键字进行过滤"
      v-model="filterText"
    ></el-input>
    <el-tree
      class="filter-tree"
      :data="data"
      default-expand-all
      :filter-node-method="filterNode"
      ref="tree"
      @node-click="nodeClick"
    >
      <span class="custom-tree-node" :class="{'current-node': currentVal.label == data.label}" slot-scope="{ node, data }">
        <svg-icon
          icon-class="组织"
          style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
          v-if="data.children"
        />
        <div v-else>
          <svg-icon
            icon-class="环卫车辆侧边栏"
            style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
            v-if="state == 'Sanitation'"
          />
          <svg-icon
            icon-class="渣土车辆侧边栏"
            style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
            v-if="state == 'muck'"
          />
          <svg-icon
            icon-class="执法车辆侧边栏"
            style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
            v-if="state== 'enforce'"
          />
          <svg-icon
            icon-class="路灯侧边栏"
            style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
            v-if="state== 'lamp'"
          />
          <svg-icon
            icon-class="视频监控侧边栏"
            style="width: 16px;height: 16px;cursor: pointer;margin-right: 6px"
            v-if="state == 'monitoring'"
          />
        </div>
        <span>{{ node.label }}</span>
      </span>
    </el-tree>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: {
    state: {
      type: String,
      default: 'Sanitation'
    }
  },
  data() {
    //这里存放数据
    return {
      filterText: '',
      data: [],
      currentVal: ''
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    filterText(val) {
      console.log(val)
      this.$refs.tree.filter(val);
    },
    state(newVal, oldVal) {
      this.dataFn()
    }
  },
  //方法集合
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    // 点击某一项
    nodeClick(data) {
      if (!data.children) {
        this.currentVal = data 
        console.log(data);
        this.$emit('chooseItem', data.location)
      }
    },
    dataFn() {
      switch (this.state) {
        case 'Sanitation':
          this.data = [{
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '环卫车辆1',
                location: { lng: 116.411, lat: 39.909 }
              }, {
                id: 10,
                label: '环卫车辆2',
                location: { lng: 116.417, lat: 39.919 }
              }]
            }]
          }]
          break;
        case 'muck':
          this.data = [{
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '渣土车辆1',
                location: { lng: 116.412, lat: 39.919 }
              }, {
                id: 10,
                label: '渣土车辆2',
                location: { lng: 116.417, lat: 39.939 }
              }]
            }]
          }]
          break;
        case 'enforce':
          this.data = [{
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '执法车辆1',
                location: { lng: 116.413, lat: 39.912 }
              }, {
                id: 10,
                label: '执法车辆2',
                location: { lng: 116.417, lat: 39.914 }
              }]
            }]
          }]
          break;
        case 'lamp':
          this.data = [{
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '路灯1',
                location: { lng: 116.411, lat: 39.929, light: true }
              }, {
                id: 10,
                label: '路灯2',
                location: { lng: 116.417, lat: 39.911, light: false }
              }]
            }]
          }]
          break;
        case 'monitoring':
          this.data = [{
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '视频监控1',
                location: { lng: 116.413, lat: 39.949 }
              }, {
                id: 10,
                label: '视频监控2',
                location: { lng: 116.417, lat: 39.911 }
              }]
            }]
          }]
          break;
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.dataFn()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {

  },
  beforeCreate() { }, //生命周期 - 创建之前
  beforeMount() { }, //生命周期 - 挂载之前
  beforeUpdate() { }, //生命周期 - 更新之前
  updated() { }, //生命周期 - 更新之后
  beforeDestroy() { }, //生命周期 - 销毁之前
  destroyed() { }, //生命周期 - 销毁完成
  activated() { }, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
