<template>
  <div style="height: 100%;">
    <div id="allmap"></div>
    <div class="close-box" @click="closeFn">
      <svg-icon icon-class="close" style="margin-top: 25px;width: 30px;height: 30px"/>
    </div>
    <div class="btns">
      <button :class="{'button-act': state === 'Sanitation'}" @click="stateFn('Sanitation')">环卫车辆</button>
      <button :class="{'button-act': state === 'muck'}" @click="stateFn('muck')">渣土车辆</button>
      <button :class="{'button-act': state === 'enforce'}" @click="stateFn('enforce')">执法车辆</button>
      <button :class="{'button-act': state === 'lamp'}" @click="stateFn('lamp')">路灯</button>
      <button :class="{'button-act': state === 'monitoring'}" @click="stateFn('monitoring')">视频监控</button>
    </div>
    <div class="mask" v-if="show_video || showCarControl"></div>
    <!-- <MapAlert></MapAlert> -->
    <!-- 车辆轨迹播放 -->
    <div class="car-control" v-show="showCarControl">
      <img class="close" src="@/assets/carClose.png" alt="" @click="closeCarControl">
      <el-date-picker
        style="width: calc(100% - 30px)"
        v-model="dateTime"
        type="datetimerange"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <div class="play-box">
        <svg-icon
          icon-class="快退"
          style="width: 20px;height: 20px;cursor: pointer;margin-right: 70px;margin-left: 24px;"
          @click.native="playTypeFn('back')"
        />
        <svg-icon
          v-if="playing_state !== 'playing'"
          icon-class="播放"
          style="width: 30px;height: 30px;cursor: pointer;margin-right: 70px;"
          @click.native="playCar('playing')"
        />
        <svg-icon
          v-else
          icon-class="暂停"
          style="width: 30px;height: 30px;margin-right: 70px;"
          @click.native="playing_state = 'pause'"
        />
        <svg-icon
          icon-class="快进"
          style="width: 20px;height: 20px;cursor: pointer;margin-right: 70px;"
          @click.native="playTypeFn('fast')"
        />
        <div class="speed">
          <span @click="showSpeed = true">倍数：{{speedList[speed_level]}}X</span>
          <div class="choose-speed" v-show="showSpeed">
            <div
              class="item"
              v-for="(item, index) in speedList"
              :key="item"
              @click="speedFn(index)"
            >{{item}}X</div>
          </div>
        </div>
      </div>
    </div>
    <!-- 视频 -->
    <div class="video-box" v-if="show_video">
      <!-- <video controls="controls" type="video/mp4" preload="auto" width="750" height="420">
        <source :src="video_src" />
      </video>-->
      <iframe frameborder="0" :src="video_src" allowfullscreen width="750" height="420"></iframe>
      <svg-icon
        icon-class="关闭"
        style="width: 30px;height: 30px;position: absolute;right: 10px;top: 10px;cursor: pointer;"
        @click.native="show_video = false"
      />
    </div>
    <map-side-bar :state="state" @chooseItem="chooseItem" v-if="!show_video && !showCarControl"></map-side-bar>
  </div>
</template>

<script>
// @ is an alias to /src
import Sanitation from '@/assets/环卫车辆@2x.png'
import muck from '@/assets/渣土车辆@2x.png'
import enforce from '@/assets/执法车辆@2x.png'
import lamp from '@/assets/路灯@2x.png'
import lampLight from '@/assets/路灯灰@2x.png'
import monitoring from '@/assets/视频@2x.png'
import close from '@/assets/关闭.png'
import closeInfo from '@/assets/close.png'
import startImg from '@/assets/起点@2x.png'
import mapSideBar from '@/components/mapSideBar'
// import MapAlert from '@/components/mapAlert'
export default {
  name: '',
  data() {
    return {
      state: 'Sanitation',
      map: {},
      show_video: false,
      video_src: '',
      dateTime: '',
      showCarControl: false,
      speedList: ['1.0', '2.0', '2.5', '3.0'],
      speed_level: 0,
      speed: '1.0',
      showSpeed: false,
      carPointList: [],
      playing_state: 'pause',
      playing_type: 'fast',
      playing_index: 0
    }
  },
  components: {
    // MapAlert
    mapSideBar
  },
  methods: {
    closeFn() {
      this.$router.go(-1)
    },
    // 清除弹出框
    clearInfoBox() {
      if (this.mapInfoBox) {
        this.mapInfoBox.close();
        this.mapInfoBox._div.remove();
        this.mapInfoBox = null;
      }
      if (this.infoBoxDetail) {
        this.infoBoxDetail.close();
        this.infoBoxDetail._div.remove();
        this.infoBoxDetail = null;
      }
    },
    // 切换显示类型 车辆  路灯  视频
    stateFn(state) {
      this.clearInfoBox();
      this.showCarControl = false;
      this.state = state;
      let arr = [];
      this.map.clearOverlays(); // 清除标记物
      switch (state) {
        case 'Sanitation':
          arr = [{ lng: 116.411, lat: 39.909 }, { lng: 116.417, lat: 39.919 }, { lng: 116.417, lat: 39.947 }];
          break;
        case 'muck':
          arr = [{ lng: 116.412, lat: 39.919 }, { lng: 116.417, lat: 39.939 }, { lng: 116.417, lat: 39.919 }];
          break;
        case 'enforce':
          arr = [{ lng: 116.413, lat: 39.912 }, { lng: 116.417, lat: 39.914 }, { lng: 116.413, lat: 39.924 }];
          break;
        case 'lamp':
          arr = [{ lng: 116.411, lat: 39.929, light: true }, { lng: 116.417, lat: 39.911, light: false }];
          break;
        case 'monitoring':
          arr = [{ lng: 116.413, lat: 39.949 }, { lng: 116.417, lat: 39.911 }];
          break;
      }
      this.markerTool(arr)
    },
    // 批量生成图标
    markerTool(arr) {

      this.clearInfoBox();// 清除弹框
      this.map.clearOverlays(); // 清除标记物
      for (let i = 0; i < arr.length; i++) {
        this.addMarker(arr[i].lng, arr[i].lat, false, arr[i])
      }
    },
    // 弹框
    getAttr(marker) {
      this.clearInfoBox();
      var p = marker.getPosition();       //获取marker的位置
      // alert("marker的位置是" + p.lng + "," + p.lat + p.content); 
      // 获取经纬度，拿经纬度请求数据插入下面的html中
      console.log(p)
      // 例  name为请求后得到的数据
      var name = '车主姓名'
      var html = ['<div></div>']
      // 车辆
      if (this.state == 'Sanitation' || this.state == 'muck' || this.state == 'enforce') {
        html = [`<div
            class="dialog-information test"
        >
            <span class="bot"></span>
            <span class="top"></span>
            <div class="header">
                <div class="title">基本信息</div>
                <img class="close" src="${closeInfo}" />
            </div>
            <div class="content">
                <div class="item">
                    <div class="left">车主姓名：${name}</div>
                    <div class="right">车牌号：浙BH1234</div>
                    <div class="clear"></div>
                </div>
                <div class="item">联系方式：14546372819</div>
                <div class="item">所属单位：市政路政管理公司</div>
                <div class="button">
                    <div class="button1 history">查看历史轨迹</div>
                    <div class="button2">查看详情</div>
                </div>
            </div>
        </div>`];
      } else if (this.state == 'lamp') { // 路灯
        html = [
          `<div
            class="dialog-information test"
        >
            <span class="bot"></span>
            <span class="top"></span>
            <div class="header">
                <div class="title">基本信息</div>
                <img class="close" src="${closeInfo}" />
            </div>
            <div class="content">
                <div class="item">
                    <div class="left">卡号：2716272829</div>
                    <div class="right">运行状态：正常</div>
                    <div class="clear"></div>
                </div>
                <div class="item">
                    <div class="left">电压：220V</div>
                    <div class="right">电流：5A</div>
                    <div class="clear"></div>
                </div>
                <div class="item">
                    <div class="left">灯损：XXX  </div>
                    <div class="right">版本信息：1.0.0</div>
                    <div class="clear"></div>
                </div>
                <div class="item">地址：浙江省宁波市鄞州区学士路655号</div>
                <div class="item">控制箱名称：1号变电箱</div>
            </div>
        </div>`
        ]
      } else { // 视频
        html = [
          `<div
            class="dialog-information test"
        >
            <span class="bot"></span>
            <span class="top"></span>
            <div class="header">
                <div class="title">基本信息</div>
                <img class="close" src="${closeInfo}" />
            </div>
            <div class="content">
                <div class="item">监控点名称：XXX</div>
                <div class="item">所属区域：宁波市鄞州区</div>
                <div class="button">
                    <div class="button1 video-button">查看实时视频</div>
                    <div class="button2">查看详情</div>
                </div>
            </div>
        </div>`
        ]
      }
      var infoBox = new BMapLib.InfoBox(this.map, html.join(""));

      infoBox.open(marker);     // 打开信息窗口

      this.mapInfoBox = infoBox;
      this.marker = marker
      var that = this;
      setTimeout(() => {
        var dom = infoBox._div;
        dom.onclick = function (e) {
          var target = e.target;
          // 点击close元素后，关闭
          if (e.target.className === 'close') {
            infoBox.close();
            infoBox = null;
            that.mapInfoBox = null;
            this.remove();
          }
          // 点击查看历史轨迹
          if (e.target.className === 'button1 history') {
            that.map.clearOverlays(); // 清除标记物
            that.carPointList = [
              { lng: 116.375829, lat: 39.909355 },
              { lng: 116.410037, lat: 39.908027 },
              { lng: 116.426997, lat: 39.910905 },
              { lng: 116.427572, lat: 39.928281 },
              { lng: 116.399401, lat: 39.933592 },
            ]
            that.clearInfoBox(); // 关闭弹框
            that.carRoute(); // 绘制路线
            that.showCarControl = true; // 显示播放
          }
          // 点击查看视频
          if (e.target.className === 'button1 video-button') {
            that.clearInfoBox();
            that.show_video = true;
            that.video_src = 'https://v.qq.com/iframe/player.html?vid=i0670jbe37a&tiny=0'
          }
          //  点击查看详情按钮，只有车辆和视频有详情按钮
          if (e.target.className === 'button2') {
            infoBox.close();
            infoBox = null;
            that.mapInfoBox = null;
            this.remove();
            var html = ['<div></div>']
            if (that.state == 'Sanitation' || that.state == 'muck' || that.state == 'enforce') {
              html = [` <div
            class="dialog-information test"
        >
            <span class="bot"></span>
            <span class="top"></span>
            <div class="header">
                <div class="title">详细信息</div>
                <img class="close" src="${closeInfo}" />
            </div>
            <div class="content">
                <div class="item">
                    <div class="left">车主姓名：王晓丽</div>
                    <div class="right">联系方式：15646362718</div>
                    <div class="clear"></div>
                </div>
                <div class="item">
                    <div class="left">车牌号：浙BH2314</div>
                    <div class="right">车身颜色：蓝色</div>
                    <div class="clear"></div>
                </div>
                <div class="item">
                    <div class="left">车辆用途：渣土车辆</div>
                    <div class="right">车辆类型：工程车</div>
                    <div class="clear"></div>
                </div>
                <div class="item">所属区域：宁波市鄞州区</div>
            </div>
        </div>`];
            } else {
              html = [` <div
            class="dialog-information test"
        >
            <span class="bot"></span>
            <span class="top"></span>
            <div class="header">
                <div class="title">详细信息</div>
                <img class="close" src="${closeInfo}" />
            </div>
            <div class="content">

                <div class="item">监控点名称：XXX</div>
                <div class="item">
                    <div class="left">类型：XXX</div>
                    <div class="right">设备IP地址：2617282929</div>
                    <div class="clear"></div>
                </div>
                <div class="item">
                    <div class="left">经度：西经20度</div>
                    <div class="right">维度：</div>
                    <div class="clear"></div>
                </div>
                <div class="item">所属区域：宁波市鄞州区</div>
            </div>
        </div>`];
            }
            var infoBoxDetail = new BMapLib.InfoBox(that.map, html.join(""));
            infoBoxDetail.open(marker);
            that.infoBoxDetail = infoBoxDetail;
            var dom = infoBoxDetail._div;
            dom.onclick = function (e) {
              if (e.target.className === 'close') {
                infoBoxDetail.close();
                infoBoxDetail = null;
                that.infoBoxDetail = null;
                this.remove();
              }
            }
          }
        };
      }, 100);
    },
    // 传入经纬度，创建图标
    addMarker(lng, lat, remove, item) {
      //创建小图标
      var pt = new BMap.Point(lng, lat);
      let img;
      let state = this.state;
      switch (state) {
        case 'Sanitation':
          img = Sanitation;
          break;
        case 'muck':
          img = muck;
          break;
        case 'enforce':
          img = enforce;
          break;
        case 'lamp':
          img = lamp;
          break;
        case 'monitoring':
          img = monitoring;
          break;
      }
      if (item && item.light) {
        img = lampLight
      }

      var myIcon = new BMap.Icon(img, new BMap.Size(36, 40), {
        imageSize: new BMap.Size(36, 40)
      }); // 创建icon
      // 如果是移动轨迹则每一次都添加删除图标，如果是创建初始化图标则监听图标点击事件
      if (remove) {
        if (this.carMk) {
          this.map.removeOverlay(this.carMk);
        }
        this.carMk = new BMap.Marker(pt, { icon: myIcon })
        this.map.addOverlay(this.carMk);              // 将标注添加到地图中
      } else {
        var marker = new BMap.Marker(pt, { icon: myIcon });  // 创建标注
        this.map.addOverlay(marker);              // 将标注添加到地图中
        // 监听小图标点击事件
        marker.addEventListener("click", () => {
          this.getAttr(marker);
        });
      }
    },
    // 根据某一车辆创建路线
    carRoute() {
      // 车辆总共经过的点
      let carPointList = this.carPointList;
      for (let i = 0; i < carPointList.length - 1; i++) {
        var startPoint = new BMap.Point(carPointList[i].lng, carPointList[i].lat);
        var endPoint = new BMap.Point(carPointList[i + 1].lng, carPointList[i + 1].lat);
        this.drawLine(startPoint, endPoint);
      }
    },
    // 绘制线
    drawLine(startPoint, endPoint) {
      var polyline = new BMap.Polyline([
        startPoint,//起始点的经纬度
        endPoint//终止点的经纬度
      ], {
          strokeColor: "#006CFF",//设置颜色 
          strokeWeight: 3, //宽度
          strokeOpacity: 1
        });//透明度
      this.map.addOverlay(polyline);
    },
    // 速度加减 快进退
    playTypeFn(val) {
      this.playing_type = val;
      // if (this.speed_level < this.speedList.length - 1) {
      //   this.speed_level++
      // }
      // this.playCar('playing')
    },
    // 播放车辆轨迹
    playCar(state) {
      var carPointList = this.carPointList;

      //创建开始图标
      var pt = new BMap.Point(carPointList[0].lng, carPointList[0].lat);
      var myIcon = new BMap.Icon(startImg, new BMap.Size(36, 40), {
        imageSize: new BMap.Size(36, 40)
      }); // 创建icon
      if (this.startCarMk) {
        this.map.removeOverlay(this.startCarMk);
      }
      this.startCarMk = new BMap.Marker(pt, { icon: myIcon })
      this.map.addOverlay(this.startCarMk);

      if (this.dd) {
        clearTimeout(this.dd)
      }
      if (this.playing_state == 'end') {
        this.playing_index = this.playing_type == 'fast' ? 0 : carPointList.length - 1;
      }
      this.playing_state = state;
      this.step = () => {
        // console.log(this.playing_index, this.playing_state)
        // 播放状态下 playing_index(当前播放位置) 大于0 或者 小于总坐标数组长度
        if ((this.playing_index < carPointList.length && this.playing_state == 'playing') || (this.playing_index >= 0 && this.playing_state == 'playing')) {
          this.addMarker(carPointList[this.playing_index].lng, carPointList[this.playing_index].lat, true);
          if (this.playing_type == 'fast') {
            this.playing_index++;
          }
          if (this.playing_type == 'back') {
            this.playing_index--;
          }
          if (this.playing_index < 0 || this.playing_index == carPointList.length) {
            this.playing_state = 'end';
            this.playing_type = 'fast'
          }

          this.dd = setTimeout(this.step, 1000 / this.speedList[this.speed_level])
        }
      }
      this.dd = this.step()
    },
    // 选择速度
    speedFn(index) {
      this.speed_level = index;
      this.showSpeed = false;
    },
    closeCarControl() {
      this.showCarControl = false;
      this.playing_state = 'end';
      this.stateFn(this.state);
    },
    // 点击侧边栏某一项时 
    chooseItem(val) {
      this.markerTool([val])
    }
  },
  mounted() {
    // 百度地图API功能
    this.map = new BMap.Map("allmap");
    var point = new BMap.Point(116.404, 39.915);
    this.map.centerAndZoom(point, 14);
    this.map.enableDragging(); // 拖拽地图
    this.map.enableScrollWheelZoom(true);
    let p = [{ lng: 116.411, lat: 39.909 }, { lng: 116.417, lat: 39.919 }, { lng: 116.417, lat: 39.947 }];
    this.markerTool(p);
    // this.map.addEventListener("click", function (e) {
    //   alert(e.point.lng + "," + e.point.lat);
    // });
  }
}
</script>
